* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik Distressed, cursive;
}

body {
  color: #fff;
  background: linear-gradient(90deg, #0074b8 0%, #ffcc29 100%);
  font-size: 16px;
}

a {
  color: #fff;
  text-decoration: none;
  position: fixed;
  top: 15px;
  left: 20px;
}

.form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  color: #fff;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
  display: flex;
}

input[type="number"] {
  width: 200px;
  height: 30px;
  color: #0074b8;
  border: 1px solid #0074b8;
  border-radius: 5px;
  margin-top: 5px;
  padding: 5px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  width: 150px;
  height: 40px;
  cursor: pointer;
  background: #0074b8;
  border: none;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background: #00578a;
}

